	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Olympic Schadenfreude","date":"21 Aug 2004","slug":"/2004-08-21-olympic-coverage/","tags":"Y2004"},"html":"<p><a href=\"http://www.pepilog.de/artikel/olympia-presswurst.htm\" title=\"Olympia-Presswurst - Pepilog\">Pepilog</a> summed up the German TV coverage of the Olympics so far. My translation: \"There's nothing wrong with saying 'you can see the athlete is disappointed', but why do they have to say things like 'that's very disappointing for us' or 'Germany is deeply disappointed with the result'? For example, in the USA when an American athlete is successful, the media will report on him extensively. If the results are not that good, they will remain silent. In Germany the successes are only briefly mentioned, but the failures are analyzed in every detail and the pain of the athletes are shown for days, even weeks.\"</p>"},"next":{"fields":{"title":"I take it all back","date":"20 Aug 2004","slug":"/2004-08-20-i-take-it-all-back/"}},"previous":{"fields":{"title":"Used Car","date":"21 Aug 2004","slug":"/2004-08-21-used-car/"}}},{"node":{"fields":{"title":"I take it all back","date":"20 Aug 2004","slug":"/2004-08-20-i-take-it-all-back/","tags":"Y2004"},"html":"<p>After saying that <a href=\"http://www.papascott.de/archives/2004/08/10/phish-me-not/\">that I hadn't heard of any phishing</a> for German online banking passwords, I've now come across three in the past two weeks. <a href=\"http://vowe.net/archives/004845.html\" title=\"vowe dot net :: Isch 'abe gar keine Postbank Konto\">The latest</a> involves the Postbank, and gets around PIN/TAN authentication by asking for a TAN directly on the fake login page. I assume the \"login\" is never successful to make you keep entering new TANs. They copied the layout pretty exactly, even the commented CVS information is in the source. If you're curious look at postbanks dot info.</p>"},"next":{"fields":{"title":"Four Weeks for a Mug","date":"20 Aug 2004","slug":"/2004-08-20-four-weeks-for-a-mug/"}},"previous":{"fields":{"title":"Olympic Schadenfreude","date":"21 Aug 2004","slug":"/2004-08-21-olympic-coverage/"}}},{"node":{"fields":{"title":"Four Weeks for a Mug","date":"20 Aug 2004","slug":"/2004-08-20-four-weeks-for-a-mug/","tags":"Y2004"},"html":"<p>According to Spiegel Online, <a href=\"http://www.spiegel.de/panorama/0,1518,314322,00.html\" title=\"München: US-Tourist wegen Maßkrug-Diebstahl vier Wochen in Haft - Panorama - SPIEGEL ONLINE\">a US tourist spent 4 weeks in custody</a> awaiting trial for stealing a beer mug (value 8) from the Hofbräuhaus in Munich. Since the student from New Mexico resisted pursuit, he was charged with robbery (usual sentence: 1 year). The judge disagreed, and sent the student home with a fine of 1260.</p>"},"next":{"fields":{"title":"Not Yet Gone","date":"19 Aug 2004","slug":"/2004-08-19-not-yet-gone/"}},"previous":{"fields":{"title":"I take it all back","date":"20 Aug 2004","slug":"/2004-08-20-i-take-it-all-back/"}}}],"pathPrefix":"","first":false,"last":false,"index":431,"pageCount":1101}}

